.trigger {
  font-size: 18px;
  line-height: 64px;
  padding: 0 24px;
  cursor: pointer;
  transition: color 0.3s;
}

.trigger:hover {
  color: #1890ff;
}

.logo {
  height: 32px;
  background: rgba(255, 255, 255, 0.2);
  margin: 16px;
}

h2 {
  color: #fff;
  font-size: 21px;
  padding-left: 12px;
}

.site-layout .site-layout-background {
  background: #fff;
}
